.searchbar {
	background: rgb(232, 232, 232);
	flex-grow: 1;
}
.searchbar:focus,
.searchbar:hover,
.submit:focus,
.submit:hover {
	background: rgb(196, 196, 196);
}
.searchbar,
.submit {
	border: 2px solid black;
	font-size: 1.1rem;
	padding: 0.25rem 1rem;
	border-radius: 10px;
	cursor: pointer;
}
.form-horizontal {
	display: flex;
	flex-grow: 1;
	width: 100%;
}
.submit {
	margin-left: 1rem;
	background: rgb(232, 232, 232);
}
@media (max-width: 600px) {
	.form-horizontal {
		flex-direction: column;
	}
	.submit {
		margin: 1rem 0 0 0;
	}
}
a {
	text-decoration: none;
	color: black;
}
